import React from 'react';

import BigChatDescription from './BigChatDescription.jsx';
import SmallChatDescription from './SmallChatDescription.jsx';
import Message from './Message.jsx';
import MessagesList from './MessagesList.jsx';
import NewMessageBox from './NewMessageBox.jsx';
import ContactsList from './ContactsList.jsx';

require('./Chat.css');

class Chat extends React.Component {
  constructor(props) {
    super(props);
    this.state={};
  }

  newMessageEvent = event => {
      console.log('Focused: ' + this.props.menuItem.caption.toUpperCase());
  }

  handleMessageAdd=(newMessage)=>{
  console.log('Chat handleMessageAdd',newMessage);
  //this.setState(this.state);

  }

  render () {
    return (
      <div className="bootstrap">
        <div className="panel panel-primary">
          <BigChatDescription chatId={this.props.chatId}/>
          <MessagesList chatId={this.props.chatId} userId={this.props.userId}/>

        </div>
      </div>
    )
  };

}

export default Chat;



