import React, { Component } from 'react';
import axios from 'axios'
import NewMessageBox from './NewMessageBox.jsx';
import Message from './Message.jsx';
import * as api from  '../api/api.js';


class MessagesList extends Component {
  constructor(props) {
    super(props);
    this.state={};
  }

  componentDidMount() {
    this.getMessagesData();
  }

  getMessagesData=()=>{
    axios.all([api.getMessagesByChatId(this.props.chatId), api.getAllUsersByChatId(this.props.chatId) ])
    .then(axios.spread((messages, users)=> {
      this.setState({messages:messages.data, users:users.data });
      var objDiv = document.getElementsByClassName('panel-body');
      document.getElementsByClassName('panel-body')[0].scrollTop = document.getElementsByClassName('panel-body')[0].scrollHeight;
    }));
  }

  handleMessageAdd=(newMessage)=>{
  //console.log('Chat handleMessageAdd',newMessage);
  this.getMessagesData();
  //this.setState(this.state);
  }

    render() {
      if( !this.state.users || !this.state.messages )
      {
        return <p>MessagesList Loading....</p>
      }
      else {
        //console.log(this.props.data);
        let currentUserId=this.props.userId;
        let users=api.toAssociativeArray(this.state.users);
        let messages=api.toAssociativeArray(this.state.messages);
        let messagesListView= new Array;

        for (let prop in messages)
        {
          let message=messages[prop];

          let isMyMessage=false;
          if(message.userId==currentUserId){
            isMyMessage=true;
          }
          messagesListView.push (<Message key = {message.id} message={message} user={users[message.userId]} isMyMessage={isMyMessage}/>);
        }

        return (
          <div>
            <div className="panel-body">
              <ul className="chat">
                {messagesListView}
              </ul>
            </div>
            <NewMessageBox onMessageAdd={this.handleMessageAdd} chatId={this.props.chatId} userId={this.props.userId}/>
          </div>

        );
      }
  }
}

export default MessagesList;



